/* src/App.css */

.App {
  text-align: center;
}

.app-logo {
  height: 40px;
  margin: 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.container-fluid {
  padding: 0;
  overflow-x: hidden;
}

.main-content {
  margin-left: 15%;
  padding: 10px;
  overflow-y: auto;
  height: 100vh;
  transition: margin-left 0.3s, width 0.3s;
  overflow-x: hidden;
}

.main-content.collapsed {
  margin-left: 10%;
  width: calc(100% - 80px);
}

.left-menu.collapsed + .main-content {
  margin-left: 80px;
  width: calc(100% - 80px);
}

.search-signin-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  margin-bottom: 20px;
}

.search-signin-container .search-bar {
  flex: 1;
}

.search-signin-container .app-logo-link {
  flex-shrink: 0;
}

.search-signin-container .btn {
  border: none;
  margin-left: 10px;
}

.bi-person-circle {
  margin-right: 5px;
}

.dark-mode {
  background-color: #121212;
  color: #ffffff;
}

.dark-mode .App-header {
  background-color: #121212;
}

.dark-mode .main-content {
  background-color: #121212;
}

.dark-mode .search-input {
  background-color: #333;
  color: #fff;
  border: 1px solid #555;
}

.dark-mode .btn {
  background-color: #555;
  color: #fff;
}

.dark-mode .list-group-item {
  background-color: #333;
  color: #fff;
}

.dark-mode .list-group-item:hover {
  background-color: #444;
}

body:not(.dark-mode) .search-input {
  background-color: #fff;
  color: #000;
  border: 1px solid #ccc;
}

body:not(.dark-mode) .btn {
  background-color: #929292;
  color: #fff;
}

.create-button {
  background-color: #007bff;
  color: white;
  border-radius: 50%;
  font-size: 1.2em;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
}

.create-button:hover {
  background-color: #0056b3;
}

.minimal-layout {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: white;
}

.signin-container,
.signup-container {
  max-width: 400px;
  width: 100%;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: white;
}

.user-info {
  display: flex;
  align-items: center;
  padding: 10px;
}

.user-info .profile-image {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}

.user-info .user-details {
  margin-right: 10px;
}

.user-info .signin-link {
  margin-left: auto;
  padding-right: 10%;
}

.user-info .signin-link .btn {
  white-space: nowrap;
}

.user-info .signin-link .signin-text {
  margin-left: 5px;
}

.user-info .update-profile-link {
  color: #007bff;
  text-decoration: none;
}

.user-info .update-profile-link:hover {
  font-size: 1.01em;
  font-weight: bold;
}

.upload-container {
  max-width: 400px;
  width: 100%;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: white;
  margin: auto;
  margin-top: 100px;
  text-align: center;
}

.upload-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

.upload-container form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.upload-container input[type="file"] {
  margin-bottom: 15px;
}

.upload-container button {
  width: 100%;
  padding: 10px;
  background-color: #4285f4;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.upload-container button:hover {
  background-color: #357ae8;
}
